<style>
  @import '~simplemde/dist/simplemde.min.css';
  @import '~highlight.js/styles/far.css';
@import "./markdown.css";
.add-new-tag-con {
  margin-top: 20px;
  border-top: 1px dashed #dbdddf;
  padding: 20px 0;
  height: 60px;
  overflow: hidden;
}
.add-new-tag-enter {
  height: 0;
  margin-top: 0;
  padding: 0px 0;
}
.add-new-tag-enter-active,
.add-new-tag-leave-active {
  transition: all 0.3s;
}
.add-new-tag-enter-to {
  height: 60px;
  margin-top: 20px;
  padding: 20px 0;
}
.add-new-tag-leave {
  height: 60px;
  margin-top: 20px;
  padding: 20px 0;
}
.add-new-tag-leave-to {
  height: 0;
  margin-top: 0;
  padding: 0px 0;
}
.margin-top-10 {
  margin-top: 10px;
}
.publish-button-con {
  border-top: 1px dashed #dbdddf;
  padding: 12px 0 0 0;
}

.markdown-editor .CodeMirror {
  height: 500px;
}
.CodeMirror-fullscreen {
  z-index: 901;
} 
</style>

<template>
    <div>
        <Form ref="formValidate" :model="addRow" :label-width="60" >
            <Row :gutter="32">
                <Col span="16">
                <Card>
                    <Row>
                        <Col span="16" offset="4">

                        <FormItem label="" prop="title" :rules="{ required: true, message: '', trigger: 'blur' }">
                            <Row>
                                <Col span="18">
                                <Input placeholder="Enter " name="title" v-model="addRow.title"></Input>
                                </Col>
                            </Row>
                        </FormItem>

                        <FormItem label="" prop="page_image">
                            <Row>
                                <Col span="18">
                                <Input placeholder="" v-model="addRow.page_image_url"> </Input>
                                </Col>
                                <Col span="6">
                                <Upload action="/api/upload" :on-success="uploadSuccess" name="image" :headers="headers">
                                    <Button type="success"></Button>
                                </Upload>
                                </Col>
                            </Row>

                        </FormItem>

                        </Col>
                    </Row>
                    <!-- <a href="#" slot="extra">
                    <Icon type="ios-loop-strong"></Icon>
                    Change
                </a> -->
                   
<vue-simplemde v-model="content" ref="markdownEditor" :configs="configs" :highlight="true" preview-class="markdown" />
                     <button type="button" @click="handleOutputHTML">HTML</button>
                    <!-- <div class="button-wrap">
      <button type="button" @click="handleOutputMARKDOWN">MARKDOWN</button>
     
      <pre v-text="output"></pre>
      <div v-html="output" v-show="type === 'html'" class="markdown"></div>
    </div> -->
                </Card>

                </Col>
                <Col span="6">
                <Card>
                    <p slot="title">
                        <Icon type="paper-airplane"></Icon>
                        
                    </p>

                    <p class="margin-top-10">
                        <Icon type="ios-calendar-outline"></Icon>&nbsp;&nbsp;
                        <span v-if="publishTimeType === 'immediately'"></span>
                        <span v-else>{{ publishTime }}</span>
                        <Button v-show="!editPublishTime" size="small" @click="handleEditPublishTime" ></Button>
                        <transition name="publish-time">
                            <div v-show="editPublishTime" class="publish-time-picker-con">
                                <div class="margin-top-10">
                                    <DatePicker @on-change="setPublishTime" type="datetime" style="width:200px;" placeholder=""></DatePicker>
                                </div>
                                <div class="margin-top-10">
                                    <Button type="primary" @click="handleSavePublishTime"></Button>
                                    <Button  @click="cancelEditPublishTime"></Button>
                                </div>
                            </div>
                        </transition>
                    </p>
                    <Row class="margin-top-10 publish-button-con">
                        <span class="publish-button">
                            <Button @click="handlePreview" icon="eye"></Button>
                        </span>
                        <span class="publish-button">
                            <Button @click="toPublish('formValidate')" :loading="publishLoading" icon="ios-checkmark" style="width:90px;" type="primary"></Button>
                        </span>
                        <span class="publish-button">
                            <Button @click="handleSaveDraft('formValidate')"></Button>
                        </span>

                    </Row>
                </Card>

                <div class="margin-top-10">
                    <Card>
                        <p slot="title">
                            <Icon type="navicon-round"></Icon>
                            
                        </p>
                        <FormItem label="" :label-width="1" prop="title" :rules="{ required: true, message: '', trigger: 'blur' }">

                            <Row>
                                <Col span="18">
                                <Select v-model="categoryId" size="small" placeholder="">
                                    <Option v-for="item in categories" :value="item.id" :key="item.id">{{ item.name }}</Option>
                                </Select>
                                </Col>
                            </Row>
                        </FormItem>
                        <!-- <CheckboxGroup v-model="offenUsedClassSelected" @on-change="setClassificationInOffen">
                        <span v-for="item in offenUsedClass" :key="item.id">
                            <Checkbox :label="item.id">{{ item.name }}</Checkbox>
                        </span>
                    </CheckboxGroup> -->

                    </Card>
                </div>
                <div class="margin-top-10">

                    <Card>
                        <p slot="title">
                            <Icon type="ios-pricetags-outline"></Icon>
                            
                        </p>
                        <FormItem label="" :label-width="1" prop="title" :rules="{ required: true, message: '', trigger: 'blur' }">

                            <Row>
                                <Col span="18">

                                <Select v-model="tagIds" multiple size="small">
                                    <Option v-for="item in tags" :value="item.id" :key="item.id">{{ item.name }}</Option>
                                </Select>
                                </Col>
                                <Col span="6">
                                <Button type="primary" v-show="!addingNewTag" @click="handleAddNewTag" size="small"></Button>
                                </Col>
                            </Row>
                        </FormItem>
                        <transition name="add-new-tag">
                            <div v-show="addingNewTag" class="add-new-tag-con">
                                <Col span="14">
                                <Input v-model="newTagName" size="small" placeholder="" />
                                </Col>
                                <Col span="5">
                                <Button @click="createNewTag" size="small" type="primary"></Button>
                                </Col>
                                <Col span="5">
                                <Button @click="cancelCreateNewTag" size="small" ></Button>
                                </Col>
                            </div>
                        </transition>
                    </Card>

                </div>

                </Col>
            </Row>
        </Form>
    </div>
</template>
<script>
import { getToken } from '@/utils/auth'
 import VueSimplemde from 'vue-simplemde'
import hljs from "highlight.js";
window.hljs = hljs;

export default {
    components: {
        VueSimplemde,
    },
    data() {
        return {
            addRow: {},
            addingNewTag: false,
            newTagName: "",
            publishTime: "",
            publishTimeType: "immediately",
            editPublishTime: false, // ,
            publishLoading: false,
            categoryId: '', // 
            classificationFinalSelected: [], // 
            content: '',
            output: '',
            type: 'html',
            categories: [],
            tags: [

            ],
            tagIds: [],
            offenUsedClass: [],
                configs: {
        spellChecker: false
      },
      headers:{

      }
          
        };
    },
    mounted() {
        this.allTags()
        this.allCategories()
        this.headers ={'Authorization': 'Bearer ' + getToken() }// token-- ['X-Token']key 

    },
    computed: {
        simplemde() {
            return this.$refs.markdownEditor.simplemde;
        },

    },
    methods: {
        async  allTags() {
            try {
                const res = await this.api.getTagList({ per_page: 100, fields: 'select:id|tag' });
                this.tags = res.data
            } catch (error) {
                const response = error.response;
                if (response) {
                    if (response.status === 401) {
                        this.$Message.error("!");
                    }
                    if (response.status === 500) {
                        this.$Message.error("!");
                    }
                }
            }
        },
        async  allCategories() {
            try {
                const res = await this.api.getCategoryList({ per_page: 100, fields: 'select:id|tag' });
                this.categories = res.data
            } catch (error) {
                const response = error.response;
                if (response) {
                    if (response.status === 401) {
                        this.$Message.error("!");
                    }
                    if (response.status === 500) {
                        this.$Message.error("!");
                    }
                }
            }
        },
        setClassificationInOffen(selectedArray) {
            this.classificationFinalSelected = selectedArray;
        },
        handleAddNewTag() {
            this.addingNewTag = !this.addingNewTag;
        },
        createNewTag() {
            if (this.newTagName.length !== 0) {
                this.addingNewTag = false;
                setTimeout(() => {
                    this.newTagName = "";
                }, 200);
            } else {
                this.$Message.error("");
            }
        },
        cancelCreateNewTag() {
            this.newTagName = "";
            this.addingNewTag = false;
        },
        handleEditPublishTime() {
            this.editPublishTime = !this.editPublishTime;
        },
        setPublishTime(datetime) {
            this.publishTime = datetime;
        },
        handleSavePublishTime() {
            this.publishTimeType = "timing";
            this.editPublishTime = false;
        },
        cancelEditPublishTime() {
            this.publishTimeType = "immediately";
            this.editPublishTime = false;
        },
        handlePreview() {
            this.$store.state.app.md = this.simplemde.value();
            this.$router.push({ name: "preview_article" });
        },
        toPublish(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    const data = {
                        ...this.addRow,
                        content: this.content,
                        content_md: this.content,
                        category_id: this.categoryId,
                        tags: this.tagIds,
                        published_at:this.publishTime,
                        state: this.publishTime? 0:1
                    }
                    this.handlePublish(data)
                } else {
                    this.$Message.error('!');
                }

            })
        },
        async handlePublish(data) {

            try {
                this.$Spin.show();
                this.publishLoading = true
                await this.api.addArticle(data);
                this.publishLoading = false
                this.$Spin.hide();
                this.$Message.success("!");
                this.$router.push({name:'articles_index'})
            } catch (error) {
                const response = error.response;
                if (response) {
                    if (response.status === 401) {
                        this.$Message.error("!");
                    }
                    if (response.status === 500) {
                        this.$Message.error("!");
                    }
                }
            }
        },
        handleSaveDraft(name) {
             this.$refs[name].validate((valid) => {
                if (valid) {
                    const data = {
                        ...this.addRow,
                        content: this.content,
                        category_id: this.categoryId,
                        tag_ids: this.tagIds,
                        status: 0,
                    }
                    this.handlePublish(data)
                } else {
                    this.$Message.error('!');
                }

            })
         },
        handleOutputHTML() {
            this.type = 'html';
            this.output = this.simplemde.markdown(this.content);
            console.log(this.output)
        },
        handleOutputMARKDOWN() {
            this.type = 'markdown';
            this.output = this.content;
        },
        uploadSuccess( response, file, fileList){
          this.$set(this.addRow, 'page_image_url', response.url)

        }
    }
};
</script>
